.Nav {
  position: relative;
  color: white;
  margin-bottom: 25px;
  padding-top: 10px;
}

.Name {
  font-size: 1.5em;
  text-align: left;
}

.Name {
  color: white;
}

.First-name {
  font-weight: 300;
}

.Last-name {
  font-weight: 600;
}

.Pages {
  margin: 5px auto;
}

.Nav-item {
  color: white;
  padding: 0 50px;
}

a.Name:focus,
a.Nav-item:focus {
  color: white;
  text-decoration: none;
}

@media (hover: hover) {
  .Name:hover {
    text-decoration: none;
  }

  a.Name:hover,
  a.Nav-item:hover {
    color: lightgray;
    text-decoration: none;
  }
}



