.SideNav {
  background-color: white;
  position: fixed;
  padding-top: 100px;
  left: -250px;
  width: 250px;
  height: 100%;
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
  z-index: 2;
}

.SideNav[open] {
  left: 0;
  -webkit-box-shadow: 0px 0px 38px -4px rgba(0, 0, 0, 0.8);
          box-shadow: 0px 0px 38px -4px rgba(0, 0, 0, 0.8);
}

.SideNav-overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  visibility: hidden;
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
}

.SideNav-overlay[open] {
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
  visibility: visible;
}

.SideNav-Item {
  padding: 5px;
}

.SideNav-Item a {
  text-decoration: none;
  color: black;
}

@media (hover: hover) {
  .SideNav-Item:hover a {
    cursor: pointer;
    color: gray;
  }
}



